import { Create, Edit, Home, Profile } from './components'

const children = [
  {
    path: '/users/create',
    component: Create,
    name: 'user.create',
    meta: { requiresAuth: true, breadcrumb: { name: 'Criar Usurio', icon: 'icon-user' } }
  },
  {
    path: '/security/profile',
    component: Profile,
    name: 'security.profile',
    meta: { requiresAuth: true, breadcrumb: { name: 'Meu Perfil', icon: 'icon-user' } }
  },
  {
    path: '/users/edit/:id',
    component: Edit,
    name: 'user.edit',
    meta: { requiresAuth: true, breadcrumb: { name: 'Editar usuario', icon: 'icon-user' } }
  }
]

export default [
  {
    children,
    name: 'user.index',
    path: '/users',
    component: Home,
    meta: { requiresAuth: true, requireRole: ['admin'], breadcrumb: { name: 'Usurios', icon: '' } }
  }
]



